/* src/culinaryMate/pages/Home/Home.css*/
.page {
  position: relative;
  z-index: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  color: white;
  text-align: center;
}

.home {
  background: linear-gradient(135deg, #6e8efb, #a777e3);
}

h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

p {
  font-size: 1.5rem;
}

/* src/pages/Pages.css に追加 */
.page h1 motion.span,
.page p motion.span {
  display: inline-block;
}

.page h1 {
  margin-bottom: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.page p {
  font-size: 1.5rem;
  line-height: 1.6;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

/* src/pages/Pages.css に追加 */
.content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.feature-item {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease;
}

.feature-item:hover {
  transform: translateY(-5px);
}

.about-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.about-section {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
}

.contact-form {
  max-width: 600px;
  margin: 40px auto 0;
}

.form-group {
  margin-bottom: 20px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  backdrop-filter: blur(10px);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

button[type="submit"] {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
}

button[type="submit"]:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}
/* src/pages/Pages.css に追加 */
.error-message {
  color: #ff4444;
  font-size: 0.9rem;
  margin-top: 5px;
  text-align: left;
}

.success-message {
  color: #4caf50;
  font-size: 1rem;
  margin: 10px 0;
  padding: 10px;
  background: rgba(76, 175, 80, 0.1);
  border-radius: 4px;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.5);
  background: rgba(255, 255, 255, 0.15);
}

button[type="submit"] {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 25px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
}

button[type="submit"]:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}
